.circles {
  margin-top: 50px;
  position: relative;
}
.circle {
  width: 70px;
  height: 70px;
  display: inline-block;
  border-radius: 50%;
  position: relative;
  opacity: 0.9;
}
.c1 {
  background-color: #6c46a7;
  -webkit-animation: moving 2s ease-in-out infinite;
          animation: moving 2s ease-in-out infinite;
}
.c2 {
  background-color: #6c46a7;
  -webkit-animation: moving2 2s ease-in-out infinite;
          animation: moving2 2s ease-in-out infinite;
}
@-webkit-keyframes moving {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    z-index: 33;
  }
  50% {
    -webkit-transform: translateX(110px) scale(1.1);
            transform: translateX(110px) scale(1.1);
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    z-index: -33;
  }
}
@keyframes moving {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    z-index: 33;
  }
  50% {
    -webkit-transform: translateX(110px) scale(1.1);
            transform: translateX(110px) scale(1.1);
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    z-index: -33;
  }
}
@-webkit-keyframes moving2 {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  50% {
    -webkit-transform: translateX(-110px) scale(1.1);
            transform: translateX(-110px) scale(1.1);
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}
@keyframes moving2 {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  50% {
    -webkit-transform: translateX(-110px) scale(1.1);
            transform: translateX(-110px) scale(1.1);
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

